import uniBadge from "@/components/uni-badge.vue";
import uniNumberBox from '@/components/uni-number-box.vue';
import myImage from '@/components/my-image.vue';
import wxParse from '@/components/mpvue-wxparse/src/wxParse.vue'
import marked from '../../../components/marked'
import {mapState,mapMutations} from 'vuex';
export default {
	components: {
		uniBadge,
		uniNumberBox,
		myImage,
		wxParse
	},
	data() {
		return {
			tabIndex:0,
			tabBars:[{
				id:'tabNum1',
				name:'商品'
			},{
				id:'tabNum2',
				name:'详情'
			}],
			tabsitem:[
				{data:['','','','',]},
				{data:['','','','',]}
			],
			swiperList:[],
			animationData: {},
			isShowOption:false,
			isShowFirst:false,
			goodsName:'',
			price:'',
			marketPrice:'',
			stock:'',
			imgUrl:'',
			memo:'',
			totalNumber:'', //购物车数量
			goodNum:1, //默认商品数量
			goodsId:'',
			optionId:''
		}
	},
	computed: mapState(['token']),
	onShow(){
		this.getShopcar();
	},
	onLoad(e) {
		if(e['data-goods-id']) {
			console.log(e);
			var goodsId = e['data-goods-id'];
			var optionId = e['data-option-id'];
			this.goodsId = goodsId;
			this.optionId = optionId;
		}
		this.isFirst();
		this.loadPagingData(goodsId)
	},
	methods: {
		isFirst(){ //是否为第一次消
			uni.request({
				url: this.websiteUrl + 'user/is_first_time',
				method: 'POST',
				data:{
					type: 1
				},
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':this.token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						console.log("获取是否为第一次消费成功："+JSON.stringify(res));
						let isFirstTime = res.data;
						if(isFirstTime == 0){
							this.isShowFirst = true;
							this.isShowOption = true;
						}
						
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		closePopover(){
			this.isShowFirst = false;
			this.isShowOption = false;
		},
		loadPagingData(goodsId){
			uni.request({
				url: this.websiteUrl + 'shop/goods_detail',
				method: 'POST',
				data:{
					id: goodsId
				},
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':this.token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						console.log("获取商品信息成功："+JSON.stringify(res));
						var data = res.data;
						var goodsName = data.goodsName;
						var price = data.price;
						var marketPrice = data.marketPrice;
						var stock = data.stock;
						var sliders = data.goodsPics.split(',');
						var imgUrl = this.getImageUrl(sliders[0]);
						var memo = this.customMethods.escape2Html(data.memo);
						this.goodsName = goodsName;
						this.price = price;
						this.marketPrice = marketPrice;
						this.stock = stock;
						this.imgUrl = imgUrl;
						this.swiperList = sliders;
						this.memo = marked(memo);
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		getShopcar(){ //获取购物车数量
			uni.request({
				url: this.websiteUrl + 'shop/cart_goods_count',
				method: 'POST',
				data:{},
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':this.token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						console.log("获取购物车数量成功："+JSON.stringify(res));
						var data = res.data;
						let totalNumber = data.totalNumber?data.totalNumber:0;
						console.log(totalNumber)
						this.totalNumber = totalNumber;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		addToCart(){
			if(this.optionId == "undefined"){this.optionId=null;}
			uni.request({
				url: this.websiteUrl + 'shop/goods_add_to_cart',
				method: 'POST',
				data:{
					goodsId:this.goodsId,
					count:this.goodNum,
					optionId:this.optionId
				},
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':this.token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						console.log("加入购物车成功："+JSON.stringify(res));
						var that = this;
						this.optionId = null;
						var msg = '加入购物车成功';
						uni.showToast({
							icon: 'success',
							title: '加入购物车成功',
							duration:2000,
							success:function(){
								that.getShopcar();
								that.animationOptionBox('100%',false);
							}
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		goLinks(){
			if(this.optionId == "undefined"){this.optionId=null;}
			uni.request({
				url: this.websiteUrl + 'shop/goods_add_to_cart',
				method: 'POST',
				data:{
					goodsId:this.goodsId,
					count:this.goodNum,
					optionId:this.optionId
				},
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':this.token},
				success: res => {
					var res = res.data;
					if(res.code == 200){
						console.log("加入购物车成功："+JSON.stringify(res));
						var that = this;
						this.optionId = null;
						var msg = '加入购物车成功';
						uni.showToast({
							icon: 'success',
							title: '加入购物车成功',
							duration:2000,
							success:function(){
								that.gotoShopCartList()
							}
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		gotoShopCartList(){
			uni.navigateTo({
			    url: '/pages/shops/shop-cart-list/shop-cart-list'
			})
		},
		onNumberChange(value){
			this.goodNum = value; 
		},
		showOptionBox(){
			this.animationOptionBox(0,true)
		},
		closeOptionBox(){
			this.animationOptionBox('100%',false)
		},
		animationOptionBox(distance,flag){
			var animation = uni.createAnimation({
				duration:300,
				timingFunction:'ease'
			})
			this.animation = animation;
			animation.translateY(distance).step();
			this.animation = animation.export();
				
			setTimeout(()=>{
				animation.translateY(distance).step();
				this.isShowOption = flag;
				this.animationData = animation.export()
			},200)
		},
		async tapTab(e){
			let index = e.target.dataset.current;
			if(this.tabIndex == index) {
				return false;
			}else {
				this.tabIndex = index;
			}
		},
		changeTab(e){
			let index = e.target.current;
			this.tabIndex = index;
		},
		toGoodDetail(){
			this.tabIndex = 1;
		}
	}
}
